<template>
  <section class="goodsitem" @click="goodsclick">
    <div class="bgimg">
      <img v-lazy="showImg" @load="imgload" :key="showImg"/>
    </div>
    <article class="text">
      <p class="title">{{ goodsitem.title }}</p>
      <div class="box">
        <p class="price">{{ price }}</p>
        <p class="cfav">{{ goodsitem.cfav }}</p>
      </div>
    </article>
  </section>
</template>

<script>
export default {
  props: {
    goodsitem: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods: {
    // 因为首页跟详情页都有下滑动问题所以这里采取了路由来判断那个页面的刷新
    imgload() {
      // 解决详情页发生变化home不应该发生变化
      // 进入相应的页面就不需要监听别的页面了
      if (this.$route.path.includes("/home")) {
        this.$bus.$emit("Homeimgitemload")
      } else if (this.$route.path.includes("/detail")) {
        this.$bus.$emit("detailimgitemload")
      }else if (this.$route.path.includes("/classification")) {
        this.$bus.$emit("classification")
      }
    },
    goodsclick() {
      // 从goodsitem数据里面拿到id传递到路由的路径里面去
      if (
        this.$route.path.indexOf("/home") != -1 ||
        this.$route.path.indexOf("/category") != -1
      ) {
        this.$router.push("/detail/" + this.goodsitem.iid)
      }
    }
  },
  computed: {
    showImg() {
      return (
        this.goodsitem.image || this.goodsitem.img || this.goodsitem.show.img
      )
    },
    price() {
      return this.goodsitem.orgPrice || "￥" + this.goodsitem.price
    }
  }
}
</script>

<style scoped>
.goodsitem {
  width: 48%;
}
.goodsitem .bgimg {
  overflow: hidden;
  border-radius: 1.6vw;
  margin-bottom: 2.1333vw;
}
.goodsitem img {
  width: 100%;
  height: 100%;
}
.goodsitem .text {
  margin-bottom: 2.1333vw;
}
.goodsitem .text .title {
  font-size: 3.7333vw;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-bottom: 2.1333vw;
}
.goodsitem .text .box {
  display: flex;
  justify-content: center;
  font-size: 3.4667vw;
  font-weight: 600;
}
.goodsitem .text .box .price {
  color: #f72585;
  margin-right: 1.3333vw;
}
.goodsitem .text .box .cfav {
  color: #666666;
}
.goodsitem .text .box .cfav::before {
  display: inline-block;
  content: "";
  width: 4vw;
  height: 4vw;
  background: url("../../../assets/img/homeImg/icon.png") no-repeat;
  background-size: 100% auto;
  vertical-align: -0.6667vw;
  margin-right: 0.5333vw;
  margin-top: -.5333vw;
}
</style>