import axios from "axios"
import Vue from "vue"
export function request(config) {
  // 创建局部的默认配置因为每次的请求接口地址都有可能不一样
  let example = axios.create({
    baseURL: "http://152.136.185.210:7878",
    // baseURL: "http://localhost:8082",
    timeout: 1000
  })

  example.interceptors.request.use(
    config => {
      // 显示loaidng
      Vue.$loading.show()
      return config
    },
    err => {
      console.log(err)
    }
  )

  // 响应拦截
  example.interceptors.response.use(
    res => {
      // 隐藏loading
      Vue.$loading.hide()
      // console.log(res);
      return res.data
    },
    err => console.log(err)
  )
  return example(config)
}
